<template>
  <div class="ui-content">
    <div class="content-list-wrap">
      <div class="content-list movie-list">
        <div class="item-text" v-for="(item, index) in items">
          <p class="text-tag">-&nbsp;{{item.tag}}&nbsp;-</p>
          <p class="text-title ellipsis-row2">
            <router-link class="ui-link" :to="'/movie/'+item.no">{{item.title}}</router-link>
          </p>
          <p class="text-author ellipsis-row1">文/{{item.author}}</p>
          <div class="text-movie-cover-background">
            <div class="text-cover-img">
              <img :src="item.imgUrl" alt="" class="img-center-x">
            </div>
          </div>
          <p class="text-content-short ellipsis-row2">{{item.text}}</p>
          <p class="text-movie-subtitle ellipsis-row1">{{item.subtitle}}</p>
          <p class="date">{{item.date}}</p>
        </div>
      </div>
      <div class="content-list-loading"></div>
      <div class="list-footer-no-more"></div>
    </div>
  </div>
</template>
<script type="text/javascript">
import data from '../../common/json/data.json'
export default {
  name: 'movie',
  data () {
    return {

    }
  },
  created () {
    this.items = data.movies
  }
}
</script>
<style type="text/css">
  .ui-content{overflow: visible;overflow-x: hidden;}
  .content-list-wrap{font-size: 1em;background: #f6f6f6;}
  .content-list{margin-top: 3.25rem;background: #f6f6f6;overflow: hidden;}
  .content-list .item-text{background: #fff;margin: 0 0 0.5em 0;box-shadow: 0 0.0625em 0 0 rgba(230,230,230,0.5);}
  .content-list p{margin: 0 1.25rem;}
  .content-list .text-tag{color: rgba(0, 0, 0, 0.6);line-height: 1.85em;font-size: 0.875em;text-align: center;padding: 0.7143em 0 1.0714em 0;}
  .content-list .text-title{font-size: 1.375em;line-height: 1.4em;max-height: 2.8em;margin-top: 0;}
  .ellipsis-row1, .ellipsis-row2 {
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1; /* 规定块级元素内显示文本的行数，必须结合下面两条属性使用 */
    display: -webkit-box;   /* 将对象设置为弹性伸缩盒子模型显示 */
    -webkit-box-orient: vertical;  /* 设置或检索伸缩盒对象的子元素的排列方式 */
  }
  .ellipsis-row2{-webkit-line-clamp: 2;}
  .content-list .text-author{font-size: 1em;line-height: 1.875em;color: rgba(0, 0, 0, 0.6);margin-top: 0.3125em;padding: 0 2.5em 0 0;}
  .content-list .text-cover-img img{position: relative;}
  .content-list .img-center-x {height: 100%;width: auto;max-width: none;left: 50%;-webkit-transform: translateX(-50%);-ms-transform: translateX(-50%);-moz-transform: translateX(-50%);
}
.content-list .text-content-short{font-size: 0.875em;line-height: 1.857em;max-height: 3.714em;color: rgba(0, 0, 0, 0.6);margin-top: 0.7143em;}

/*以上为公共样式*/
    .content-list .text-movie-cover-background{height: 13.75em;width: 100%;padding:1.8125em 0px;background-image: url(../../../static/image/movie/movie-list-background.png);-webkit-background-size: 100%;
    background-size: 100%;}
    .content-list .text-cover-img {margin: 0.625em 1.25em;width: 20.9375em;height: 12.5em;background-size: cover;background-image: url(../../../static/image/movie/list-paceholder.png);overflow: hidden;}
    .content-list .text-movie-cover-background .text-cover-img{border-radius: 0.25em;box-shadow: 0 0 0.3125em 0.1875em rgba(230,230,230,0.5);}
    .content-list .text-movie-subtitle{text-align: right;font-size: 0.875em;line-height: 1.857em;color: rgba(0,0,0,0.6);}
    .content-list .date{font-size: 0.625em;line-height: 1.4em;padding:1.8em 0 2.5em 0;color: 808080;text-align: left;}

</style>



