<template>
  <div class="ui-content">
    <div class="content-list-wrap">
      <div class="content-list music-list">
        <div class="item-text" v-for="(item, index) in items">
          <p class="text-tag">-&nbsp;{{item.tag}}&nbsp;-</p>
          <p class="text-title ellipsis-row2">
            <router-link :to="'/music/'+item.no">{{item.title}}</router-link>
          </p>
          <p class="text-author ellipsis-row1">文/{{item.author}}</p>
          <div class="text-music-cover">
            <div class="text-music-cover-background"></div>
            <div class="text-music-cover-img">
              <img :src="item.imgUrl" alt="" class="img-center-y">
            </div>
            <div class="music-play music-detail-status">
              <img src="../../../static/image/music/play_btn_empty.png" alt="" class="play-btn" width="100%">
            </div>
            <img :src="item.platform" alt="" class="platform-icon">
          </div>
          <p class="text-music-author ellipsis-row1">{{item.musicAuthor}}</p>
          <p class="text-content-short ellipsis-row2">{{item.text}}</p>
          <p class="date">2017/8/20</p>
        </div>
      </div>
      <div class="content-list-loading"></div>
      <div class="content-list-footer-no-more"></div>
    </div>
  </div>
</template>
<script type="text/javascript">
import data from '../../common/json/data.json'
export default {
  name: 'music',
  data () {
    return {

    }
  },
  created () {
    this.items = data.musics
  }
}
</script>
<style type="text/css">
  .ui-content{overflow: visible;overflow-x: hidden;}
  .content-list-wrap{font-size: 1em;background: #f6f6f6;}
  .content-list{margin-top: 3.25rem;background: #f6f6f6;overflow: hidden;}
  .content-list .item-text{background: #fff;margin: 0 0 0.5em 0;box-shadow: 0 0.0625em 0 0 rgba(230,230,230,0.5);}
  .content-list p{margin: 0 1.25rem;}
  .content-list .text-tag{color: rgba(0, 0, 0, 0.6);line-height: 1.85em;font-size: 0.875em;text-align: center;padding: 0.7143em 0 1.0714em 0;}
  .content-list .text-title{font-size: 1.375em;line-height: 1.4em;max-height: 2.8em;margin-top: 0;}
  .ellipsis-row1, .ellipsis-row2 {
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1; /* 规定块级元素内显示文本的行数，必须结合下面两条属性使用 */
    display: -webkit-box;   /* 将对象设置为弹性伸缩盒子模型显示 */
    -webkit-box-orient: vertical;  /* 设置或检索伸缩盒对象的子元素的排列方式 */
  }
  .ellipsis-row2{-webkit-line-clamp: 2;}
  .content-list .text-author{font-size: 1em;line-height: 1.875em;color: rgba(0, 0, 0, 0.6);margin-top: 0.3125em;padding: 0 2.5em 0 0;}
  .content-list .text-music-cover{position: relative;clear: both; height: 15.125em;margin: 0.625em 0;background: url(../../../static/image/music/music-list-right.png) no-repeat center right;background-size: auto 100%;}
  .content-list .text-music-cover-background{position: absolute;left: 0;top:0; width: 18.6875em;height: 15.125em;border-radius: 0 9.9em 10em 0;box-shadow: 0 0 0.625em 0.125em rgba(230,230,230,0.5);}
  .content-list .text-music-cover-img{position: absolute;left: 4.0625em;top:0.5em; width: 14.125em;height: 14.125em;border-radius: 9.6em 9.6875em 9.6875em 9.6875em;-webkit-background-size: cover;background-image: url(../../../static/image/music/music-list-paceholder.png);
  background-size: cover;background-image: url(../../../static/image/music/music-list-paceholder.png);overflow: hidden;}
  .content-list .text-music-cover-img img{position: relative;border-radius: 9.6em 9.6875em 9.6875em 9.6875em;}
  .content-list .img-center-y{width: 100%;height: auto;max-height: none;top:50%; -webkit-transform:translateY(-50%);-ms-transform: translateY(-50%);-moz-transform: translateY(-50%); }
  .content-list .music-play{position: absolute;left:9.75em; top:6.1875em;width: 2.75em;height: 2.75em;}
  .music-detail-status .play-btn{display: inline-block;}
  .content-list .platform-icon{position: absolute;left: 1.25rem;bottom: 0.5em;width: 1.5em;height: 1.5em}
.content-list .text-music-author{font-size: 0.625em;line-height: 1.4em;color: #808080;margin-top: 1em;}
.content-list .text-content-short{font-size: 0.875em;line-height: 1.857em;max-height: 3.714em;color: rgba(0, 0, 0, 0.6);margin-top: 0.7143em;}
.content-list .date{font-size: 0.625em;line-height: 1.4em;padding:1.8em 0 2.5em 0;color: 808080;text-align: left;}
</style>



